* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}

.search-form {
  min-height: 40px;
  width: 100%;
  background-color: #6495ed;
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
  position: fixed;
  top: 0;
}

input {
  text-align: center;
}

ul {
  list-style: none;
}

:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.gallery {
  max-width: 1140px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 240px;
  justify-content: center;
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.photo-card {
  margin-left: auto;
  margin-right: auto;
  display: block;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.gallery__image:hover {
  transform: scale(1.03);
}

.gallery__image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.gallery__link {
  height: 100%;
  text-decoration: none;
  display: block;
}

.gallery__link:hover {
  cursor: zoom-in;
}

.info {
  justify-content: center;
  gap: 10px;
  display: flex;
}

.info-item {
  color: #000;
  flex-direction: column;
  align-items: center;
  font-size: 10px;
  line-height: 1.5;
  display: flex;
}

